import React, { Component } from 'react';
import { Table,Pagination } from "antd"
import "./style/style.scss";
import Nav from "@/components/topNav"
import Times from "@/components/Time/";
import dataed from "@/config/tableList";
import { observer, inject } from 'mobx-react';
import Bread from "@/components/Bread/"

@inject('AllList')
@observer
class Detail extends Component {
    state = {
        open: false,
        brr: ["今日", "昨日", "本周", "本月", "最近7日", "最近30日"],
        arr: [
            "考勤明细统计", "打卡清单", "请假清单", "出差清单", "外出清单", "加班清单"
        ],
        ind: 0,
        listed:[],
        pageid:0,
        limit:10,
        lim:10
    }
    render() {
        let { brr, arr, ind,limit } = this.state;
        let {AllList} = this.props;
        let {allList,size} = AllList;
        allList = allList.map(item=>{return item});
        return (
            <div className="wrap">
                <Bread history={this.props.history}/>
                <Nav navArr={arr} switchFn={this.switchFn} />
                <Times title="审批日期" btnTitle="批量审批" brrList={brr} buttonOpen={true} buttonTitle="导出" />
                <div className="detail-bottom">
                    {ind === 1 &&
                    <div>
                        <Table dataSource={allList} columns={dataed.detailList} pagination={false}>
                        </Table>
                        <Pagination defaultCurrent={limit} total={size*1} onChange={this.pageList}/>
                    </div>}
                    {ind === 0 &&
                    <div>
                        <Table dataSource={allList} columns={dataed.detailDate} pagination={false}></Table>
                        <Pagination defaultCurrent={limit} total={size*1} onChange={this.pageList}/>
                        </div>
                    }
                </div>
            </div>
        )
    }
    switchFn = (index) => {
        this.setState({
            ind: index
        })
    }
    async componentDidMount(){
        let {limit,pageid} = this.state;
        await this.props.AllList.all(limit,pageid);
        let data = this.props.AllList.allList.list
        this.setState({
            listed:data
        })
    }
      //分页
      pageList = (page,pageSize)=>{
        this.props.AllList.all(pageSize,page-1)
     }
}
export default Detail;